/* src/Keyboard.css */
.keyboard {
    display: inline-block;
    margin: 20px auto;
}

.keyboard-row {
    display: flex;
    justify-content: center;
}

.key {
    width: 40px;
    height: 40px;
    margin: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #f0f0f0;
    font-size: 16px;
    transition: background-color 0.2s;
}

.key.active {
    background-color: yellow;
}

.key.highlight {
    background-color: lightblue;
}

.key.space {
    width: 200px;
}
